@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  width: 100%;
  height: 100%;
  color: #000000;
  background-color: #ffffff;
}

a {
  display: block;
  color: #000000;
  text-decoration: none;
  user-select: none;
  cursor: pointer;
}
@media (hover: hover) {
  a:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
a.focus-visible:focus {
  opacity: 0.5;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
p,
figure {
  margin: 0;
}

svg {
  display: block;
}

img,
video {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

textarea {
  resize: none;
}

[data-validate=picture] {
  display: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input [type=email],
input [type=number],
input [type=password],
input [type=search],
input [type=tel],
input [type=text],
input [type=url] {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  user-select: none;
  cursor: pointer;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}
.wrapper::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 43, 102, 0.5);
  z-index: 1;
  opacity: 0;
  transition: 0.5s;
  visibility: hidden;
}
.wrapper.overlay::before {
  opacity: 1;
  transition: 0.5s;
  visibility: visible;
}
@media (max-width: 767px) {
  .wrapper--nojs .nav--opened {
    display: block;
    position: absolute;
    background-color: #f9fbfd;
    top: 0;
    right: 0;
    width: 100%;
    transition: 0.5s;
    margin-top: -20px;
    padding-top: 20px;
    min-height: 250px;
    z-index: 15;
  }
}
.wrapper--nojs .entry {
  padding-top: 300px;
}
.wrapper--nojs .btn-close {
  display: none;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: flex;
  justify-content: center;
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  padding: 11px 0;
  border: none;
  background-color: #f9fbfd;
  width: 100%;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.btn.focus-visible:focus {
  opacity: 0.7;
}

.btn-open {
  display: none;
}
@media (max-width: 767px) {
  .btn-open {
    display: block;
    width: 24px;
    height: 22px;
    border: none;
    background-color: transparent;
  }
  .btn-open:disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .btn-open:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
@media (max-width: 767px) {
  .btn-open.focus-visible:focus {
    opacity: 0.8;
  }
}
.btn-open::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 2px;
  top: 5px;
  right: 16px;
  background-color: #f9fbfd;
  box-shadow: 0 5px 0 0 #f9fbfd, 0 10px 0 0 #f9fbfd;
  transition: 0.5s;
}
@media (hover: hover) {
  .btn-open::before:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn-open::before.focus-visible:focus {
  opacity: 0.8;
}

.btn-close {
  display: none;
  top: 25px;
  right: 15px;
}
@media (max-width: 767px) {
  .btn-close {
    display: block;
    position: absolute;
    width: 24px;
    height: 22px;
    border: none;
    background-color: transparent;
  }
  .btn-close:disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .btn-close:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
@media (max-width: 767px) {
  .btn-close.focus-visible:focus {
    opacity: 0.8;
  }
}
.btn-close::before, .btn-close::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 2px;
  background-color: #011c40;
  right: 5px;
}
@media (hover: hover) {
  .btn-close::before:hover:not(.focus-visible), .btn-close::after:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn-close::before.focus-visible:focus, .btn-close::after.focus-visible:focus {
  opacity: 0.8;
}
.btn-close::before {
  transform: rotate(45deg);
  box-shadow: none;
  transition: 0.5s;
}
.btn-close::after {
  transform: rotate(-45deg);
  transition: 0.5s;
}

.header {
  color: #f9fbfd;
  background-color: transparent;
  padding: 35px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
@media (max-width: 1023px) {
  .header {
    padding: 30px 0;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 20px 0;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__wrapper > img {
  transition: 0.5s;
}
@media (max-width: 1023px) {
  .header__wrapper > img {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__wrapper > img {
    width: 42px;
    height: 22px;
  }
}

.entry {
  color: #f9fbfd;
  background-color: #639ddb;
  padding: 283px 0;
  background-image: image-set(url("../img/entry/entry-bg.jpg") 1x, url("../img/entry/entry-bg.webp") 1x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .entry {
    background-image: image-set(url("../img/entry/entry-bg@2x.jpg") 2x, url("../img/entry/entry-bg@2x.webp") 2x);
  }
}
@media (max-width: 1023px) {
  .entry {
    padding: 280px 0;
  }
}
@media (max-width: 767px) {
  .entry {
    padding: 185px 0;
  }
}

.entry__wrapper {
  margin-left: 95px;
}
@media (max-width: 1023px) {
  .entry__wrapper {
    margin-left: 0;
  }
}

.entry h1 {
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .entry h1 {
    font-size: 50px;
    line-height: 61px;
    margin-bottom: 29px;
  }
}
@media (max-width: 767px) {
  .entry h1 {
    font-size: 36px;
    line-height: 45px;
    margin-bottom: 25px;
  }
}

.entry p {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .entry p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .entry p {
    font-size: 16px;
  }
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 767px) {
  .nav__list--footer {
    display: block;
    margin-bottom: 30px;
  }
}

.nav__item {
  margin-right: 33px;
}
.nav__item:last-child {
  margin-right: 0;
  margin-bottom: 0;
}
@media (max-width: 1023px) {
  .nav__item {
    margin-right: 23px;
  }
}
@media (max-width: 767px) {
  .nav__item {
    margin-bottom: 23px;
  }
}

.nav__link {
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
  padding: 9px 0;
}
@media (max-width: 1023px) {
  .nav__link {
    padding: 0;
    font-size: 14px;
    line-height: 17px;
  }
  .nav__link--footer {
    font-size: 12px;
    line-height: 15px;
  }
}

@media (max-width: 767px) {
  .nav--opened {
    display: block;
    position: fixed;
    background-color: #f9fbfd;
    top: 0;
    right: -100%;
    width: 90%;
    transition: 0.5s;
    padding-top: 20px;
    min-height: 100%;
    z-index: 15;
  }
}

@media (max-width: 767px) {
  .nav--opened .nav__list {
    flex-direction: column;
    align-items: flex-start;
    margin-left: 25px;
  }
}

@media (max-width: 767px) {
  .nav--opened .nav__link {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

.nav--opened img {
  display: none;
}
@media (max-width: 767px) {
  .nav--opened img {
    display: block;
    margin-left: 15px;
    margin-bottom: 43px;
  }
}

